<script>

import {getSampleScripts, getSampleScriptTypes} from '@/lib/sampleScripts.js'
import $util from '@/lib/util.js'

import ContextPanel from '@/components/panels/ContextPanel.vue'
import IconUp from '@/components/icons/IconUp.vue'
import IconDown from '@/components/icons/IconDown.vue'
//import IconReset from '@/components/icons/IconReset.vue'
//import IconEdit from '@/components/icons/IconEdit.vue'

export default {
  name: 'Main',
  components: {
    ContextPanel,
    IconUp, IconDown
  },
  methods: {
    loadFont() {
        if (!this.fontPanelForm.fontName) {
            this.fontPanelForm.fontName = "font01";
        }
        let url = this.fontPanelForm.fontURL;
        let urlString = `url(${url})`;
        let f = new FontFace(this.fontPanelForm.fontName, urlString);
        let that = this;
        f.load().then(function(font) {
            document.fonts.add(font);
            let loadedFont = {
                "name": font.family,
                "font": font
            };
            that.fonts.push(loadedFont);
        });
    },
    onFontUpload(e) {
        const file = e.target.files[0];
        let url = URL.createObjectURL(file);
        let urlString = `url(${url})`;
        if (!this.fontPanelForm.fontName) {
            this.fontPanelForm.fontName = "font01";
        }
        var f = new FontFace(this.fontPanelForm.fontName, urlString);
        var that = this;
        f.load().then(function(font) {
            document.fonts.add(font);
            let loadedFont = {
                "name": font.family,
                "font": font
            };
            that.fonts.push(loadedFont);
        });
    },
    setCurrentTool(tool) {
        this.currentTool = tool;
        if (tool == 'pencil') {
            window.ctx.lineWidth = 3;
            window.ctx.filter = 'none';
        } else if (tool == 'brush') {
            window.ctx.lineWidth = 20;
            window.ctx.filter = 'blur(3px)';
        }
        this.updateCurrentContextDetails();
    },
    onCanvasMouseUp(event) { // eslint-disable-line no-unused-vars
        this.isMouseDown = false;
        if (this.currentTool == 'line') {
            this.drawLine();
            this.updateCanvasAllLayers();
            this.updateCurrentLayerCanvas();
        } else if (this.currentTool == 'rectangle') {
            this.drawRect();
            this.updateCanvasAllLayers();
            this.updateCurrentLayerCanvas();
        } else if (this.currentTool == 'circle') {
            this.drawCircle();
            this.updateCanvasAllLayers();
            this.updateCurrentLayerCanvas();
        } else if (this.currentTool == 'ellipse') {
            this.drawEllipse();
            this.updateCanvasAllLayers();
            this.updateCurrentLayerCanvas();
        }
    },
    onCanvasMouseDown(event) { // eslint-disable-line no-unused-vars
        this.isMouseDown = true;
        this.updateMouseDownPosition(event);
        if (this.currentTool == 'pencil' || this.currentTool == 'brush') {
            this.paintBrush();
            this.updateCanvasAllLayers();
            this.updateCurrentLayerCanvas();
        }
    },
    onCanvasMouseMove(event) {
        this.updateMousePosition(event);
        if (this.isMouseDown) {
            if (this.currentTool == 'pencil' || this.currentTool == 'brush') {
                this.paintBrush();
            }
        }
        this.updateCanvasAllLayers();
        this.updateCurrentLayerCanvas();
    },
    drawLine() {
        let ctx = window.ctx;
        ctx.beginPath();
        ctx.moveTo(this.mouseDownPosition.x, this.mouseDownPosition.y);
        ctx.lineTo(this.mousePosition.x, this.mousePosition.y);
        ctx.stroke();
    },
    drawRect() {
        let ctx = window.ctx;
        ctx.beginPath();
        ctx.rect(this.mouseDownPosition.x, this.mouseDownPosition.y, this.mousePosition.x - this.mouseDownPosition.x, this.mousePosition.y - this.mouseDownPosition.y);
        ctx.stroke();
    },
    drawCircle() {
        let centerX = (this.mousePosition.x + this.mouseDownPosition.x) / 2;
        let centerY = (this.mousePosition.y + this.mouseDownPosition.y) / 2;
        let dx = Math.abs(this.mousePosition.x - this.mouseDownPosition.x);
        let dy = Math.abs(this.mousePosition.y - this.mouseDownPosition.y);
        let radius = Math.min(dx, dy) / 2;

        let ctx = window.ctx;
        ctx.beginPath();
        ctx.arc(centerX, centerY, radius, 0, 2 * Math.PI);
        ctx.stroke();
    },
    drawEllipse() {
        let x = (this.mousePosition.x + this.mouseDownPosition.x) / 2;
        let y = (this.mousePosition.y + this.mouseDownPosition.y) / 2;
        let radiusX = Math.abs(this.mousePosition.x - this.mouseDownPosition.x) / 2;
        let radiusY = Math.abs(this.mousePosition.y - this.mouseDownPosition.y) / 2;
        let rotation = 0;
        let startAngle = 0;
        let endAngle = 2 * Math.PI;
        let anticlockwise = false;

        let ctx = window.ctx;
        ctx.beginPath()
        ctx.ellipse(x, y, radiusX, radiusY, rotation, startAngle, endAngle, anticlockwise);
        ctx.stroke();
    },
    paintBrush() {
        //let currentLayerCtx = this.currentLayer.canvas.getContext('2d');
        ctx.save(); // eslint-disable-line no-undef
        ctx.beginPath(); // eslint-disable-line no-undef
        ctx.lineCap = 'round'; // eslint-disable-line no-undef
        ctx.moveTo(this.previousMousePosition.x, this.previousMousePosition.y); // eslint-disable-line no-undef
        ctx.lineTo(this.mousePosition.x, this.mousePosition.y); // eslint-disable-line no-undef
        ctx.stroke(); // eslint-disable-line no-undef
        ctx.restore(); // eslint-disable-line no-undef
    },
    updateMousePosition(event) {
        let canvas = null;
        if (event.target.id=="canvasAllLayers") {
            canvas = document.getElementById('canvasAllLayers');
        } else if (event.target.id=="canvasCurrentLayer") {
            canvas = document.getElementById('canvasCurrentLayer');
        }
        this.previousMousePosition.x = this.mousePosition.x;
        this.previousMousePosition.y = this.mousePosition.y;
        this.mousePosition.x = event.clientX - canvas.offsetLeft; 
        this.mousePosition.y = event.clientY - canvas.offsetTop; 
    },
    updateMouseDownPosition(event) {
        let canvas = null;
        if (event.target.id=="canvasAllLayers") {
            canvas = document.getElementById('canvasAllLayers');
        } else if (event.target.id=="canvasCurrentLayer") {
            canvas = document.getElementById('canvasCurrentLayer');
        }
        this.mouseDownPosition.x = event.clientX - canvas.offsetLeft; 
        this.mouseDownPosition.y = event.clientY - canvas.offsetTop; 
    },
    editGlobalVariable(variableName) {
        let value = prompt('New value', '');
        let type = prompt('New type', 'number');

        if (type == 'number') {
            value = Number(value);
        }

        this.globalVariables[variableName] = value;

        this.$forceUpdate();
    },
    deleteGlobalVariable(name) {
        delete this.globalVariables[name];
        this.$forceUpdate();
    },
    popupAddGlobalVariable() {
        let variableName = prompt('Variable name', '');
        let type = prompt('Type', 'number');
        let value = prompt('Value', '');

        if (type == 'number') {
            value = Number(value);
        }

        this.addGlobalVariable(variableName, value);

        this.$forceUpdate();
    },
    addGlobalVariable(variableName, value) {
        this.globalVariables[variableName] = value;
    },
    combineLayers(baseLayerName, maskLayerName) {
        let baseLayer = this.getLayerByName(baseLayerName);
        let baseCtx = baseLayer.canvas.getContext('2d');
        let baseImageData = baseCtx.getImageData(0, 0, 600, 400); //TODO: remover hard-coded

        let maskLayer = this.getLayerByName(maskLayerName);
        let maskCtx = maskLayer.canvas.getContext('2d');
        let maskImageData = maskCtx.getImageData(0, 0, 600, 400); //TODO: remover hard-coded

        for (let i = 0; i < baseImageData.data.length; i += 4) {
            baseImageData.data[i+3] = baseImageData.data[i+3] * (maskImageData.data[i+3] / 255);
        }

        baseCtx.putImageData(baseImageData, 0, 0);
    },
    updateCanvasAllLayers() {
        let ctxAllLayers = document.getElementById('canvasAllLayers').getContext('2d');
        ctxAllLayers.save();
        ctxAllLayers.clearRect(0,0,600,400);
        this.drawTiledBackground(ctxAllLayers)
        this.drawAllLayers(ctxAllLayers);
        ctxAllLayers.restore();
    },
    updateCurrentLayerCanvas() {
        if (this.currentLayer != null) {
            let currentLayerCtx = document.getElementById('canvasCurrentLayer').getContext('2d');

            this.drawTiledBackground(currentLayerCtx);

            // draw current layer
            currentLayerCtx.save();
            //currentLayerCtx.clearRect(0,0,600,400);
            currentLayerCtx.globalAlpha = this.currentLayer.alpha;
            currentLayerCtx.drawImage(this.currentLayer.canvas, 0, 0);
            currentLayerCtx.restore();
        }
    },
    /*
    clearCanvas() {
        let backgroundCtx = document.getElementById('canvas').getContext('2d');
        backgroundCtx.save();
        backgroundCtx.clearRect(0,0,600,400);
        backgroundCtx.restore();
    },
    */
    clearCurrentLayer() {
        ctx.clearRect(0,0,600,400); // eslint-disable-line no-undef
        this.updateCurrentLayerCanvas();
        this.updateCanvasAllLayers();
    },
    clearFilters() {
        ctx.filter = 'none'; // eslint-disable-line no-undef
        this.updateCurrentContextDetails();
    },
    saveContext() {
        ctx.save(); // eslint-disable-line no-undef
    },
    restoreContext() {
        ctx.restore(); // eslint-disable-line no-undef
        this.updateCurrentContextDetails();
    },
    clip() {
        ctx.clip(); // eslint-disable-line no-undef
    },
    resetTransform() {
        ctx.setTransform(1, 0, 0, 1, 0, 0); // eslint-disable-line no-undef
        this.updateCurrentContextDetails();
    },
    resetContextAttribute(attribute) {
        let ctx = window.ctx;
        if (attribute=='fillStyle') { ctx.fillStyle = '#000000'; }
        else if (attribute=='strokeStyle') { ctx.strokeStyle = '#000000'; }
        else if (attribute=='font') { ctx.font = '10px sans-serif'; }
        else if (attribute=='globalAlpha') { ctx.globalAlpha = '1'; }
        else if (attribute=='lineWidth') { ctx.lineWidth = '1'; }
        else if (attribute=='lineCap') { ctx.lineCap = 'butt'; }
        else if (attribute=='lineJoin') { ctx.lineJoin = 'miter'; }
        else if (attribute=='filter') { ctx.filter = 'none'; }
        else if (attribute=='shadowBlur') { ctx.shadowBlur = '0'; }
        else if (attribute=='shadowColor') { ctx.shadowColor = 'rgba(0, 0, 0, 0)'; }
        else if (attribute=='shadowOffsetX') { ctx.shadowOffsetX = '0'; }
        else if (attribute=='shadowOffsetY') { ctx.shadowOffsetY = '0'; }
        else if (attribute=='lineDashOffset') { ctx.lineDashOffset = 0; }
        else if (attribute=='textAlign') { ctx.textAlign = 'start'; }
        else if (attribute=='miterLimit') { ctx.miterLimit = 10; }
        else if (attribute=='textBaseline') { ctx.textBaseline = 'alphabetic'; }
        else if (attribute=='globalCompositeOperation') { ctx.globalCompositeOperation = 'source-over'; }

        else if (attribute=='lineDash') { ctx.setLineDash([]); }

        this.updateCurrentContextDetails();
    },
    editLayer(layerName) {
        let layer = this.getLayerByName(layerName);
        layer.name = prompt("Layer name", "");

        let newAlpha = prompt("Layer alpha", layer.alpha);
        layer.alpha = parseFloat(newAlpha);

        this.updateCurrentLayerCanvas();
        this.updateCanvasAllLayers();
    },
    buildBackgroundCanvas() {
        let canvas = document.createElement('canvas');
        canvas.width = 600;
        canvas.height = 400;

        let backgroundCtx = canvas.getContext('2d');
        backgroundCtx.clearRect(0, 0, 600, 400); 

        let firstColDark = true;
        let dark = true;

        let tileSize = 10;

        for (let x = 0; x < 600; x += tileSize) {
            dark = firstColDark;
            for (let y = 0; y < 600; y += tileSize) {
                if (dark) {
                    backgroundCtx.fillStyle = "#cccccc";
                } else {
                    backgroundCtx.fillStyle = "#eeeeee";
                }
                backgroundCtx.fillRect(x, y, tileSize, tileSize);
                dark = !dark;
            }
            firstColDark = !firstColDark;
        }

        return canvas;
    },
    addLayerWithName(layerName) {
        let canvas = document.createElement('canvas');
        canvas.width = 600;
        canvas.height = 400;
        canvas.getContext('2d').clearRect(0, 0, 600, 400); 

        let canvasLayer = {
            "name": layerName,
            "alpha": 1,
            "canvas": canvas,
            "visible": true
        };

        this.canvasLayers.push(canvasLayer);
    },
    addLayer() {
        let layerName = prompt("Layer name", "");
        this.addLayerWithName(layerName);
    },
    duplicateCurrentLayer() {
        this.duplicateLayer(this.currentLayer);
    },
    duplicateLayer(layer, newLayerName) {
        if (newLayerName == null) {
            newLayerName = prompt("New layer name", "");
        }

        if (typeof layer == "string") {
            layer = this.getLayerByName(layer);
        }

        let canvas = document.createElement('canvas');
        canvas.width = 600;
        canvas.height = 400;

        let ctx = canvas.getContext('2d');
        ctx.drawImage(layer.canvas, 0, 0);

        let canvasLayer = {
            "name": newLayerName,
            "alpha": layer.alpha,
            "canvas": canvas,
            "visible": layer.visible
        };

        this.canvasLayers.push(canvasLayer);
    },
    deleteLayerByName(layerName) {
        this.deleteLayer(this.getLayerByName(layerName));
    },
    deleteLayer(layer) {
        let index = this.getLayerIndex(layer);
        this.canvasLayers.splice(index, 1);

        if (layer == this.currentLayer) {
            if (this.canvasLayers.length > 0) {
                this.setCurrentLayer(this.canvasLayers[0].name);
            } else {
                //this.setBackgroundLayer();
                //TODO: verificar
            }
        }

        this.updateCanvasAllLayers();
    },
    /*
    renderLayers() {
        for (let i=0; i<this.canvasLayers.length; i++) {
            let backgroundCtx = document.getElementById('canvas').getContext('2d');
            backgroundCtx.save();
            backgroundCtx.globalAlpha = this.canvasLayers[i].alpha;
            backgroundCtx.drawImage(this.canvasLayers[i].canvas, 0, 0);
            backgroundCtx.restore();
        }
    },
    */
    /*
    renderLayer(layerName) {
        let layer = this.getLayerByName(layerName);
        let backgroundCtx = document.getElementById('canvas').getContext('2d');
        backgroundCtx.save();
        backgroundCtx.globalAlpha = layer.alpha;
        backgroundCtx.drawImage(layer.canvas, 0, 0);
        backgroundCtx.restore();
    },
    */
    renderLayerToContext(layer, ctx) {
        ctx.save();
        ctx.globalAlpha = layer.alpha;
        ctx.drawImage(layer.canvas, 0, 0);
        ctx.restore();
    },
    setCurrentLayer(name) {
        let layer = this.getLayerByName(name);
        if (layer != null) {
            this.currentLayer = layer;
            //TODO: mudar nome da variável para não confundir com this.ctx
            window.ctx = layer.canvas.getContext('2d'); // eslint-disable-line no-undef
            this.updateCurrentContextDetails();
        }

        this.updateCurrentLayerCanvas();
    },
    getLayerByName(name) {
        for (let i=0; i<this.canvasLayers.length; i++) {
            let layer = this.canvasLayers[i];
            if (layer.name == name) {
                return layer;
            }
        }
        return null;
    },
    getCurrentLayerIndex() {
        for (let i=0; i<this.canvasLayers.length; i++) {
            let layer = this.canvasLayers[i];
            if (layer == this.currentLayer) {
                return i;
            }
        }
        return null;
    },
    getLayerIndex(layer) {
        for (let i=0; i<this.canvasLayers.length; i++) {
            let l = this.canvasLayers[i];
            if (l == layer) {
                return i;
            }
        }
        return null;
    },
    /*
    setBackgroundLayer() {
        ctx = document.getElementById('canvas').getContext('2d');

        this.currentLayer = null; //TODO: verificar
        this.updateCurrentLayerCanvas();
        this.updateCurrentContextDetails();
    },
    */
    getCurrentLayer() {
        return this.currentLayer;
    },
    getCurrentCanvas() {
        return this.getCurrentLayer().canvas;
    },
    getCurrentContext() {
        return this.getCurrentCanvas().getContext('2d');
    },
    updateCurrentContextDetails() {

        let canvas = null;
        if (this.currentLayer == null) {
            //canvas = document.getElementById('canvas');
            return;
        } else {
            canvas = this.currentLayer.canvas;
        }

        this.ctx.fillStyle = canvas.getContext('2d').fillStyle;
        this.ctx.strokeStyle = canvas.getContext('2d').strokeStyle;
        this.ctx.lineWidth = canvas.getContext('2d').lineWidth;
        this.ctx.lineCap = canvas.getContext('2d').lineCap;
        this.ctx.lineJoin = canvas.getContext('2d').lineJoin;
        this.ctx.miterLimit = canvas.getContext('2d').miterLimit;
        this.ctx.font = canvas.getContext('2d').font;
        this.ctx.textAlign = canvas.getContext('2d').textAlign;
        this.ctx.globalAlpha = canvas.getContext('2d').globalAlpha;
        this.ctx.globalCompositeOperation = canvas.getContext('2d').globalCompositeOperation;

        this.ctx.transform = canvas.getContext('2d').getTransform();
        this.ctx.lineDash = canvas.getContext('2d').getLineDash();

        this.ctx.filter = canvas.getContext('2d').filter;
        this.ctx.shadowBlur = canvas.getContext('2d').shadowBlur;
        this.ctx.shadowColor = canvas.getContext('2d').shadowColor;
        this.ctx.shadowOffsetX = canvas.getContext('2d').shadowOffsetX;
        this.ctx.shadowOffsetY = canvas.getContext('2d').shadowOffsetY;
        this.ctx.lineDashOffset = canvas.getContext('2d').lineDashOffset;
        this.ctx.textBaseline = canvas.getContext('2d').textBaseline;
    },
    executeSavedScript: function (scriptName, params) {
        for (let i=0; i<this.savedScripts.length; i++) {
            if (this.savedScripts[i].name == scriptName) {
                if (params == null) {
                    params = {};
                }
                let returnValue = this.savedScripts[i].func(params);
                //this.update();
                //this.renderLayers();
                this.updateCurrentLayerCanvas();
                this.updateCanvasAllLayers();
                this.updateCurrentContextDetails();
                return returnValue;
            }
        }
    },
    executeSampleScript: function (scriptName, params) {
        for (let i=0; i<this.allSampleScripts.length; i++) {
            if (this.allSampleScripts[i].name == scriptName) {
                if (params == null) {
                    params = {};
                }
                this.allSampleScripts[i].func(params);
            }
        }
        //this.update();
        //this.renderLayers();

        this.updateCurrentLayerCanvas();
        this.updateCanvasAllLayers();
        this.updateCurrentContextDetails();
    },
    loadSavedScript: function (scriptName) {
        for (let i=0; i<this.savedScripts.length; i++) {
            if (this.savedScripts[i].name == scriptName) {
                document.getElementById("sourceCode").value = this.savedScripts[i].source;
                this.currentScriptName = scriptName;
                return;
            }
        }
    },
    loadSampleScript: function (scriptName) {
        this.currentScriptName = null;
        for (let i=0; i<this.sampleScripts.length; i++) {
            if (this.sampleScripts[i].name == scriptName) {
                document.getElementById("sourceCode").value = this.sampleScripts[i].source;
            }
        }
    },
    selectSampleScript: function (scriptName) {
        for (let i=0; i<this.sampleScripts.length; i++) {
            if (this.sampleScripts[i].name == scriptName) {
                this.selectedSampleScript = this.sampleScripts[i];
            }
        }
    },
    unselectSampleScript: function () {
        this.selectedSampleScript = null;
    },
    copySampleSourceCode: function () {
        //var copyText = document.getElementById("selectedSampleScriptSourceCode");
        //copyText.select(); 
        //document.execCommand("copy");
        var range = document.createRange();
        range.selectNode(document.getElementById("selectedSampleScriptSourceCode"));
        window.getSelection().removeAllRanges(); // clear current selection
        window.getSelection().addRange(range); // to select text
        document.execCommand("copy");
        window.getSelection().removeAllRanges();// to deselect
    },
    executeScript: function () {
        let sourceCode = document.getElementById("sourceCode").value;
        this.dinamicFunction = new Function("params", sourceCode);
        let params = {};
        this.dinamicFunction(params);
        //this.update();
        //this.renderLayers();

        this.updateCurrentLayerCanvas();
        this.updateCanvasAllLayers();
        this.updateCurrentContextDetails();
    },
    newScript: function () {
        this.currentScriptName = null;
        document.getElementById("sourceCode").value = "";
    },
    saveScriptAs: function () {
        this.currentScriptName = prompt("Script name", "");
        this.saveScript();
    },
    saveScript: function () {
        if (this.currentScriptName == null || this.currentScriptName == "") {
            this.currentScriptName = prompt("Script name", "");
        }
        let sourceCode = document.getElementById("sourceCode").value;
        let savedScript = {
            "name": this.currentScriptName,
            "func": new Function("params", sourceCode),
            "source": sourceCode
        };

        for (let i=0; i<this.savedScripts.length; i++) {
            if (this.savedScripts[i].name == this.currentScriptName) {
                this.savedScripts[i] = savedScript;
                return;
            }
        }

        this.savedScripts.push(savedScript);
    },
    moveLayerUp() {
        let currentLayerIndex = this.getCurrentLayerIndex();
        this.arrayMove(this.canvasLayers, currentLayerIndex, currentLayerIndex-1);
        this.updateCanvasAllLayers();
    },
    moveLayerDown() {
        let currentLayerIndex = this.getCurrentLayerIndex();
        this.arrayMove(this.canvasLayers, currentLayerIndex, currentLayerIndex+1);
        this.updateCanvasAllLayers();
    },
    arrayMove(arr, fromIndex, toIndex) {
        var element = arr[fromIndex];
        arr.splice(fromIndex, 1);
        arr.splice(toIndex, 0, element);
    },
    saveScriptsToLocalStorage() {
        localStorage.savedScripts = JSON.stringify(this.savedScripts);
    },
    loadScriptsFromLocalStorage() {
        //console.log("loadScriptsFromLocalStorage");
        this.savedScripts = JSON.parse(localStorage.savedScripts);
        for (let i=0; i<this.savedScripts.length; i++) {
            //console.log("i=" +i);
            let savedScript = this.savedScripts[i];
            savedScript.func = new Function("params", savedScript.source)
            //console.log("savedScript.sourceCode=" + savedScript.source);
        }
    },
    filterSampleScripts() {
        this.sampleScripts = getSampleScripts(this.sampleScriptsFilter.type, this.sampleScriptsFilter.text);
        this.numFilteredSampleScripts = this.sampleScripts.length;
    },
    clearSearch() {
        this.sampleScriptsFilter.type = "";
        this.sampleScriptsFilter.text = "";
        this.filterSampleScripts();
        this.numFilteredSampleScripts = this.sampleScripts.length;
    },
    resetContext() {
        ctx.fillStyle = '#000000'; // eslint-disable-line no-undef
        ctx.strokeStyle = '#000000'; // eslint-disable-line no-undef
        ctx.lineWidth = 1; // eslint-disable-line no-undef
        ctx.lineCap = 'butt'; // eslint-disable-line no-undef
        ctx.lineJoin = 'miter'; // eslint-disable-line no-undef
        ctx.miterLimit = 10; // eslint-disable-line no-undef
        ctx.font = '10px sans-serif'; // eslint-disable-line no-undef
        ctx.textAlign = 'start'; // eslint-disable-line no-undef
        ctx.globalAlpha = 1; // eslint-disable-line no-undef
        ctx.globalCompositeOperation = 'source-over'; // eslint-disable-line no-undef
        ctx.filter = 'none'; // eslint-disable-line no-undef
        ctx.shadowBlur = 0; // eslint-disable-line no-undef
        ctx.shadowOffsetX = 0; // eslint-disable-line no-undef
        ctx.shadowOffsetY = 0; // eslint-disable-line no-undef
        ctx.shadowColor = 'rgba(0, 0, 0, 0)'; // eslint-disable-line no-undef
        ctx.lineDashOffset = 0; // eslint-disable-line no-undef
        ctx.textBaseline = 'alphabetic'; // eslint-disable-line no-undef

        ctx.setTransform(1, 0, 0, 1, 0, 0); // eslint-disable-line no-undef
        ctx.setLineDash([]); // eslint-disable-line no-undef

        this.updateCurrentContextDetails()
    },
    fillScreenWhite() {
        this.executeSampleScript('Fill screen white');
    },
    fillScreenBlack() {
        this.executeSampleScript('Fill screen black');
    },
    onImageUpload(e) {
        const file = e.target.files[0];
        let image = {
            "filename": file.name,
            "url": URL.createObjectURL(file)
        };
        this.images.push(image);
    },
    repaintCurrentLayer() {
        this.executeSampleScript('Repaint');
    },
    togglePanelVisibility(panelName) {
        this.visiblePanels[panelName] = !this.visiblePanels[panelName];
    },
    log(message) {
        this.logs.push(message);
    },
    clearLogs() {
        this.logs = [];
    },
    drawImageToCurrentLayer(imageUrl) {
        var img = new Image();
        img.src = imageUrl;
        ctx.drawImage(img, 0, 0); // eslint-disable-line no-undef

        this.updateCurrentLayerCanvas();
        this.updateCanvasAllLayers();
    },
    exportCanvasAllLayers() {
        let canvas = document.createElement('canvas');
        canvas.width = 600;
        canvas.height = 400;

        let ctx = canvas.getContext('2d');
        this.drawAllLayers(ctx);

        let dataURL = canvas.toDataURL("image/png");
        window.open(dataURL);
    },
    exportCanvasAllLayersCropped() {
        let originalCanvas = document.createElement('canvas');
        originalCanvas.width = 600;
        originalCanvas.height = 400;

        let ctx = originalCanvas.getContext('2d');
        this.drawAllLayers(ctx);

        let croppedcanvas = $util.crop(originalCanvas); // eslint-disable-line no-undef

        let dataURL = croppedcanvas.toDataURL("image/png");
        window.open(dataURL);
    },
    drawAllLayers(ctx) {
        for (let i=this.canvasLayers.length-1; i>=0; i--) {
            let layer = this.canvasLayers[i];
            if (layer.visible) {
                ctx.save();
                ctx.globalAlpha = layer.alpha;
                ctx.drawImage(layer.canvas, 0, 0);
                ctx.restore();
            }
        }
    },
    drawTiledBackground(ctx) {
        ctx.save();
        ctx.globalAlpha = 1;
        ctx.drawImage(this.backgroundCanvas, 0, 0);
        ctx.restore();
    }
  },
  data() {
    return {
      currentScriptName: null,
      dinamicFunction: new Function("params", ""),
      savedScripts: [],
      sampleScripts: [],
      allSampleScripts: [],
      numSampleScripts: null,
      numFilteredSampleScripts: null,
      selectedSampleScript: null,
      canvasLayers: [],
      backgroundCanvas: null,
      globalVariables: {},
      viewType: "MAIN_CANVAS",
      //currentCanvasLayerIndex: 0,
      currentLayer: null,
      ctx: {
          fillStyle: null,
          strokeStyle: null,
          lineWidth: null,
          lineCap: null,
          lineJoin: null,
          miterLimit: null,
          font: null,
          textAlign: null,
          globalAlpha: null,
          globalCompositeOperation: null,
          transform: null
      },
      sampleScriptsFilter: {
          type: "",
          text: ""
      },
      sampleScriptTypes: [],
      images: [],
      fonts: [],
      visiblePanels: {
          layers: true,
          context: false,
          images: false,
          globalVariables: false,
          fonts: false,
          logs: false
      },
      logs: [],
      mousePosition: {
          x: 0,
          y: 0
      },
      previousMousePosition: {
          x: 0,
          y: 0
      },
      mouseDownPosition: {
          x: 0,
          y: 0
      },
      isMouseDown: false,
      currentTool: "pencil", //pencil, brush, line, rect, circle
      fontPanelForm: {
          fontName: '',
          fontURL: ''
      }
    }
  },
  mounted () {
    window.app = this;
    window.$util = $util;

    this.allSampleScripts = getSampleScripts();
    this.sampleScripts = getSampleScripts();
    this.sampleScriptTypes = getSampleScriptTypes();

    this.numSampleScripts = this.allSampleScripts.length;
    this.numFilteredSampleScripts = this.allSampleScripts.length;

    this.backgroundCanvas = this.buildBackgroundCanvas();

    let initialLayerName = 'layer01';
    this.addLayerWithName(initialLayerName);
    this.setCurrentLayer(initialLayerName);

    this.updateCanvasAllLayers();
    this.updateCurrentContextDetails();

    document.getElementById('canvasAllLayers').addEventListener('mousemove', this.onCanvasMouseMove);
    document.getElementById('canvasAllLayers').addEventListener('mouseup', this.onCanvasMouseUp);
    document.getElementById('canvasAllLayers').addEventListener('mousedown', this.onCanvasMouseDown);

    document.getElementById('canvasCurrentLayer').addEventListener('mousemove', this.onCanvasMouseMove);
    document.getElementById('canvasCurrentLayer').addEventListener('mouseup', this.onCanvasMouseUp);
    document.getElementById('canvasCurrentLayer').addEventListener('mousedown', this.onCanvasMouseDown);
  }

}

</script>

