<script>
import IconEdit from '@/components/icons/IconEdit.vue'
import IconReset from '@/components/icons/IconReset.vue'

export default {
  name: 'ContextPanel',
  components: {
    IconEdit, IconReset
  },
  props: {
  },
  data() {
    return {
      currentAttribute: null,
      newCtx: {
        fillStyle: null
      }
    }
  },
  methods: {
    editContextAttribute(attribute) {
      this.currentAttribute = attribute;
      this.loadContextAttributeValues();
    },
    loadContextAttributeValues() {
      this.newCtx.fillStyle = window.ctx.fillStyle;
      this.newCtx.strokeStyle = window.ctx.strokeStyle;
      this.newCtx.font = window.ctx.font;
      this.newCtx.globalAlpha = window.ctx.globalAlpha;
      this.newCtx.lineWidth = window.ctx.lineWidth;
      this.newCtx.lineCap = window.ctx.lineCap;
      this.newCtx.lineJoin = window.ctx.lineJoin;
      this.newCtx.filter = window.ctx.filter;
      this.newCtx.shadowBlur = window.ctx.shadowBlur;
      this.newCtx.shadowColor = window.ctx.shadowColor;
      this.newCtx.shadowOffsetX = window.ctx.shadowOffsetX;
      this.newCtx.shadowOffsetY = window.ctx.shadowOffsetY;
      this.newCtx.lineDashOffset = window.ctx.lineDashOffset;
      this.newCtx.textAlign = window.ctx.textAlign;
      this.newCtx.miterLimit = window.ctx.miterLimit;
      this.newCtx.textBaseline = window.ctx.textBaseline;
      this.newCtx.globalCompositeOperation = window.ctx.globalCompositeOperation;

      this.newCtx.lineDashStr = "[" + window.ctx.getLineDash().toString() + "]";
    },
    updateContextAttribute() {
      if (this.currentAttribute=='fillStyle') { window.ctx.fillStyle = this.newCtx.fillStyle; }
      else if (this.currentAttribute=='strokeStyle') { window.ctx.strokeStyle = this.newCtx.strokeStyle; }
      else if (this.currentAttribute=='font') { window.ctx.font = this.newCtx.font; }
      else if (this.currentAttribute=='globalAlpha') { window.ctx.globalAlpha = this.newCtx.globalAlpha; }
      else if (this.currentAttribute=='lineWidth') { window.ctx.lineWidth = this.newCtx.lineWidth; }
      else if (this.currentAttribute=='lineCap') { window.ctx.lineCap = this.newCtx.lineCap; }
      else if (this.currentAttribute=='lineJoin') { window.ctx.lineJoin = this.newCtx.lineJoin; }
      else if (this.currentAttribute=='filter') { window.ctx.filter = this.newCtx.filter; }
      else if (this.currentAttribute=='shadowBlur') { window.ctx.shadowBlur = this.newCtx.shadowBlur; }
      else if (this.currentAttribute=='shadowColor') { window.ctx.shadowColor = this.newCtx.shadowColor; }
      else if (this.currentAttribute=='shadowOffsetX') { window.ctx.shadowOffsetX = this.newCtx.shadowOffsetX; }
      else if (this.currentAttribute=='shadowOffsetY') { window.ctx.shadowOffsetY = this.newCtx.shadowOffsetY; }
      else if (this.currentAttribute=='lineDashOffset') { window.ctx.lineDashOffset = this.newCtx.lineDashOffset; }
      else if (this.currentAttribute=='textAlign') { window.ctx.textAlign = this.newCtx.textAlign; }
      else if (this.currentAttribute=='miterLimit') { window.ctx.miterLimit = this.newCtx.miterLimit; }
      else if (this.currentAttribute=='textBaseline') { window.ctx.textBaseline = this.newCtx.textBaseline; }
      else if (this.currentAttribute=='globalCompositeOperation') { window.ctx.globalCompositeOperation = this.newCtx.globalCompositeOperation; }
      
      else if (this.currentAttribute=='lineDash') {
        window.ctx.setLineDash(this.newCtx.lineDashStr.replaceAll("[","").replaceAll("]","").split(","));
      }

      this.$parent.updateCurrentContextDetails();
    }
  }
}
</script>

